<template>
    <div>
        <!-- <div>
            <span></span>
            <el-input type="textarea" v-model="z1" class="zz"></el-input>
            <span></span>
            <el-input type="textarea" v-model="z2" class="zz"></el-input>
            <span></span>
            <el-input type="textarea" v-model="z3" class="zz"></el-input>
            <span></span>
            <el-input type="textarea" v-model="z4" class="zz"></el-input>
            <el-button type="primary" @click="submitForm()"></el-button>
        </div> -->
        <div id="myChart" :style="{width: '300px', height: '200px'}"></div>
        <el-button type="primary" @click="submitForm()"></el-button>
    </div>
</template>

<script>
    export default {
        
        data() {
            return {
                z1: '',
                z2: '',
                z3: '',
                z4: '',
                zzz: 1,
            }
        },
        mounted () {
            this.drawLine();
        },
        created() {
            
        },
        methods:{
            submitForm(){
                // console.log(this.codeList);
                this.zzz++;
                this.drawLine();
            },
            // 
            drawLine(){
                // domecharts
                let myChart = this.$echarts.init(document.getElementById('myChart'))
                // 
                myChart.setOption({
                    title: {
                        text: '',
                        // subtext: '',
                        left: 'center'
                    },
                    tooltip: {
                        trigger: 'item'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                    },
                    series: [
                        {
                            name: '',
                            type: 'pie',
                            radius: '50%',
                            data: [
                                {value: this.zzz, name: ''},
                                {value: 5, name: 'AI'},
                            ],
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                });
            },
        },
    }
</script>

<style scoped>

</style>



